import React from "react";
import PropTypes from "prop-types";
import TextInput from "../common/TextInput";
import SelectInput from "../common/SelectInput";
import DateInput from "../common/DateInput";

const PassengerForm = ({
  passenger,
  onSave,
  onChange,
  errors = {},
  saving = false,
}) => {
  return (
    <form onSubmit={onSave}>
      <h2>{passenger.id ? "Edit" : "Add"} Passenger</h2>
      {errors.onSave && (
        <div className="alert alert-danger" role="alert">
          {errors.onSave}
        </div>
      )}
      <TextInput
        name="name"
        label="Name"
        value={passenger.name}
        onChange={onChange}
        error={errors.name}
      />

      <TextInput
        name="seat"
        label="Seat No."
        value={passenger.seat}
        onChange={() => {}}
        error={errors.seat}
      />

      <SelectInput
        name="wheelChair"
        label="wheelChair"
        value={
          passenger && null !== passenger.wheelChair ? passenger.wheelChair : ""
        }
        defaultOption="WheelChair required?"
        options={[
          {
            value: true,
            text: "YES",
          },
          {
            value: false,
            text: "NO",
          },
        ]}
        onChange={onChange}
      />

      <TextInput
        name="infants"
        label="Infants"
        inputType="number"
        value={passenger.infants}
        onChange={onChange}
      />

      <SelectInput
        name="passport"
        label="Passport"
        value={
          passenger && null !== passenger.passport ? passenger.passport : ""
        }
        defaultOption="passport available?"
        options={[
          {
            value: true,
            text: "YES",
          },
          {
            value: false,
            text: "NO",
          },
        ]}
        onChange={onChange}
      />

      <TextInput
        name="address"
        label="Address"
        value={passenger.address}
        onChange={onChange}
      />

      <DateInput
        name="dob"
        label="Date of birth"
        value={passenger.dob}
        onChange={onChange}
      />

      <button type="submit" className="btn btn-primary">
        {saving ? "Saving..." : "Save"}
      </button>
    </form>
  );
};

PassengerForm.propTypes = {
  passenger: PropTypes.object.isRequired,
  errors: PropTypes.object,
  onSave: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  saving: PropTypes.bool,
};

export default PassengerForm;
