import React, { useEffect, useState } from "react";
import PassengerForm from "./PassengerForm";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import {
  loadPassengers,
  savePassenger,
} from "../../redux/actions/passengerActions";
import { newPassenger, newFlight } from "../../../tools/mockData";
import SeatMapPage from "../seatmap/SeatMapPage";
import { loadFlights } from "../../redux/actions/flightActions";
import Spinner from "../common/Spinner";
import { toast } from "react-toastify";

function ManagePassengerPage({
  passengers,
  loadPassengers,
  loadFlights,
  savePassenger,
  flightName,
  flights,
  history,
  ...props
}) {
  const [passenger, setPassenger] = useState({ ...props.passenger });
  const [flightPassengers, setFlightPassengers] = useState([
    ...props.flightPassengers,
  ]);
  const [saving, setSaving] = useState(false);
  const [errors, setErrors] = useState({});

  useEffect(() => {
    if (flights.length === 0) {
      loadFlights().catch((error) => alert("Loading flights failed " + error));
    }
    if (passengers.length === 0) {
      loadPassengers().catch((error) =>
        alert("Loading passengers failed " + error)
      );
    } else if (flights.length > 0) {
      setPassenger({ ...props.passenger });
      setFlightPassengers([...props.flightPassengers]);
    }
  }, [props.flightPassengers]);

  function handleClick(seat) {
    const status =
      passenger.wheelChair && passenger.infants > 0
        ? "both"
        : passenger.infants > 0
        ? "infants"
        : passenger.wheelChair
        ? "wheelChair"
        : "reserved";
    setPassenger((prevPassenger) => {
      const seatReserved =
        flightPassengers.filter((passenger) => passenger.seat === seat.number)
          .length > 0;
      return {
        ...prevPassenger,
        seat:
          seatReserved && prevPassenger.seat === seat.number
            ? ""
            : seatReserved
            ? prevPassenger.seat
            : seat.number,
        status:
          seatReserved && prevPassenger.seat === seat.number
            ? "available"
            : status,
      };
    });

    setFlightPassengers((prevPassengers) => {
      const seatReserved =
        flightPassengers.filter((passenger) => passenger.seat === seat.number)
          .length > 0;
      return prevPassengers.map((prevPassenger) => {
        return prevPassenger.id === passenger.id
          ? prevPassenger.seat === seat.number
            ? { ...prevPassenger, status: "available", seat: "" }
            : seatReserved
            ? prevPassenger
            : { ...prevPassenger, seat: seat.number, status: status }
          : prevPassenger;
      });
    });
  }

  function formIsValid() {
    const { name } = passenger;
    const errors = {};

    if (!name) errors.name = "Name is required.";
    // if (!authorId) errors.author = "Author is required";
    // if (!category) errors.category = "Category is required";

    setErrors(errors);
    // Form is valid if the errors object still has no properties
    return Object.keys(errors).length === 0;
  }
  function handleSave(event) {
    event.preventDefault();
    if (!formIsValid()) return;
    setSaving(true);
    savePassenger(passenger)
      .then(() => {
        setSaving(false);
        toast.success("Passenger saved");
        history.push("/admin/passengers/" + flightName);
      })
      .catch((error) => {
        setSaving(false);
        setErrors({ onSave: error.message });
      });
  }

  function handleChange(event) {
    const { name, value } = event.target;
    setPassenger((prevPassenger) => {
      if (name === "passport" || name === "wheelChair") {
        return {
          ...prevPassenger,
          [name]: value === "true",
        };
      }
      return {
        ...prevPassenger,
        [name]: value,
      };
    });
  }

  return (
    <>
      {passengers.length === 0 || flights.length === 0 ? (
        <Spinner />
      ) : (
        <>
          <div className="row">
            <div className="col-md-7">
              <PassengerForm
                passenger={passenger}
                onSave={handleSave}
                onChange={handleChange}
                saving={saving}
                errors={errors}
              ></PassengerForm>
            </div>
            <div className="col-md-5">
              <SeatMapPage
                passengers={flightPassengers}
                passenger={passenger}
                select={handleClick}
                onSave={handleSave}
                backUrl={"/admin/passengers/" + flightName}
                isAdmin={true}
              ></SeatMapPage>
            </div>
          </div>
        </>
      )}
    </>
  );
}

ManagePassengerPage.propTypes = {
  passengers: PropTypes.array.isRequired,
  flights: PropTypes.array.isRequired,
  flightPassengers: PropTypes.array.isRequired,
  flightName: PropTypes.string.isRequired,
  loadPassengers: PropTypes.func.isRequired,
  loadFlights: PropTypes.func.isRequired,
  passenger: PropTypes.object.isRequired,
  savePassenger: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

function getPassengerByPNR(passengers, pnr) {
  return passengers.find((a) => a.pnr === pnr) || newPassenger;
}
function getFlightByName(flights, name) {
  return flights.find((a) => a.name === name) || null;
}

function mapStateToProps(state, ownProps) {
  const flightName = ownProps.match.params.name;
  const pnr = ownProps.match.params.pnr;

  const flight =
    flightName && state.flights.length > 0
      ? getFlightByName(state.flights, flightName)
      : newFlight;

  const flightPassengers = flight.id
    ? state.passengers.filter((passenger1) => passenger1.flightId === flight.id)
    : [];

  const newPnr =
    state.passengers.length > 0 ? "IND" + (state.passengers.length + 101) : "";
  let passenger =
    pnr && state.passengers.length > 0
      ? getPassengerByPNR(state.passengers, pnr)
      : { ...newPassenger, pnr: newPnr, flightId: flight.id };

  return {
    passenger,
    flightPassengers,
    flightName,
    passengers: state.passengers,
    flights: state.flights,
  };
}

const mapDispatchToProps = {
  loadPassengers,
  savePassenger,
  loadFlights,
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ManagePassengerPage);
